body {
  color: #333;
  background-color: #f9f9f9;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  font-family: Arial, sans-serif;
  display: flex;
}

.cat-info {
  flex-direction: column;
  align-items: center;
  gap: 20px;
  display: flex;
}

.cats-info {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.cat-temperament {
  width: 100%;
  max-width: 600px;
  text-align: center;
}

.cat-name {
  color: #007bff;
  text-transform: uppercase;
  margin-bottom: 10px;
  font-size: 28px;
  font-weight: bold;
}

.breed-select {
  width: 100%;
  max-width: 600px;
  height: 50px;
  color: #333;
  text-align-last: center;
  background-color: #fff;
  border: 2px solid #007bff;
  border-radius: 10px;
  outline: none;
  padding: 10px;
  font-size: 18px;
  transition: background-color .3s, color .3s;
}

.cat-description {
  width: 100%;
  max-width: 600px;
  text-align: center;
}

h1, h2 {
  margin: 0;
}

.temperament-span {
  color: #007bff;
  font-style: italic;
  font-weight: bold;
}

.loader {
  height: 100vh;
  justify-content: center;
  align-items: center;
  display: flex;
}

.hidden {
  display: none;
}

.loader-spinner {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 3px solid #007bff;
  border-radius: 50%;
  transition: border-color .3s;
  animation: rotation 2s linear infinite;
  display: inline-block;
  position: relative;
}

.loader-spinner:after {
  content: "";
  box-sizing: border-box;
  width: 24px;
  height: 24px;
  transform-origin: center;
  border: 3px solid #fff;
  border-radius: 50%;
  margin: auto;
  animation: rotationBack 1s linear infinite;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes rotationBack {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(-360deg);
  }
}

img {
  max-width: 100%;
  height: auto;
  border-radius: 10px;
  transition: transform .3s;
  box-shadow: 0 4px 6px rgba(0, 0, 0, .1);
}

img:hover {
  transform: scale(1.05);
}

